@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;

.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: vars.$c-main-black;
  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;
    margin-bottom: 50px;
  }
  &__footer {
    margin: 0;
    padding: 0;
  }
}
