@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;

.button {
  display: block;
  text-decoration: none;
  text-align: center;
  width: 100%;
  max-width: 280px;
  box-sizing: border-box;
  height: 50px;
  border-radius: 8px;
  background-color: vars.$c-main-blue;
  color: #fff;
  font-size: 15px;
  line-height: 50px;
  border: 2px solid vars.$c-main-blue;
  transition:
    background-color vars.$transition-time,
    color vars.$transition-time;

  &:hover {
    background-color: #fff;
    color: vars.$c-main-blue;
  }
}
