@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;

.services {
  @include mix.page-grid;

  gap: 20px;

  &__box {
    text-align: left;
    grid-column: 1 / 3;

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-desktop-small {
      grid-column: 1 / 5;
    }
  }

  &__title {
    &--small {
      font-size: 13px;
      font-weight: 700;
      line-height: 19.5px;
      letter-spacing: 3px;
      text-transform: uppercase;
      color: $c-text-white-grey;
    }
  }

  &__text {
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    color: vars.$c-text-grey;
    margin-bottom: 48px;
  }

  &__card {
    background-color: #fff;
    padding: 32px;
    box-shadow: 0 9px 18px 0 #25293108;
    border-radius: 16px;
    text-align: left;
    grid-column: 1 / 3;

    @include hover(scale, 1.1);

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-desktop-small {
      grid-column: 7 / 10;
    }

    &--up {
      @include on-tablet {
        grid-column: 4 / 7;
        transform: translateY(-50%);
      }

      @include on-desktop-small {
        grid-column: 10 / 13;
      }
    }

    &--number {
      width: 46px;
      height: 24px;
      background-color: #2060f61a;
      color: vars.$c-main-blue;
      border-radius: 23px;
      margin-bottom: 24px;
      text-align: center;
    }

    &--text {
      font-family: 'Open Sans', sans-serif;
      font-size: 13px;
      font-weight: 600;
      line-height: 19.5px;
      color: vars.$c-text-grey;
      margin-bottom: 32px;
    }

    &--link {
      display: flex;
      gap: 16px;
      align-items: center;
      text-decoration: none;

      &-icon {
        background-color: vars.$c-main-blue;
        border-radius: 50%;
        width: 40px;
        height: 40px;
      }

      &-text {
        color: #334563;
        font-size: 15px;
        line-height: 22.5px;
        text-align: center;
      }
    }
  }
}
