@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;
@use '../utils/extends' as ext;

.slider {
  position: relative;
  grid-column: 1 / 3;
  overflow: hidden;

  @include on-tablet {
    grid-column: 1 / 7;
  }

  @include on-desktop-small {
    grid-column: 7 / 13;
  }

  &__images {
    height: 390px;
    position: relative;

    @include on-desktop-small {
      height: 100%;
    }
  }

  &__img {
    display: block;
    min-width: 100%;
    width: 100%;
    max-height: 390px;
    object-fit: cover;
    border-radius: 30px;
    position: absolute;
    left: 0;
    bottom: 0;
    opacity: 0;
    transition: opacity vars.$transition-time ease-in-out;

    @media (max-width: 410px) {
      max-height: 100%;
      height: auto;
    }

    @include on-desktop-small {
      max-height: 100%;
      height: 100%;
      width: 100%;
      border-radius: 30px 0;
    }

    &.active {
      opacity: 1;
    }
  }

  &__bottom {
    position: absolute;
    left: 20px;
    bottom: 24px;
    z-index: 2;

    @include on-tablet {
      left: 41px;
      bottom: 32px;
    }

    @include on-desktop-small {
      left: 78px;
      bottom: 48px;
      display: flex;
      align-items: center;
      gap: 24px;
    }
  }

  &__buttons {
    display: flex;
    gap: 16px;
    margin-bottom: 16px;
  }

  &__title {
    margin: 0;
    padding: 0;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    margin-bottom: 8px;
  }

  &__text {
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
  }
}
