@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;
@use '../utils/extends' as ext;

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px 0;
  margin: 0 auto;

  @include on-tablet {
    padding: 20px 41px 0;
  }

  @include on-desktop-small {
    padding: 49px 54px 0;
  }

  @include on-desktop-large {
    padding: 49px 104px 0;
  }

  &__logo {
    width: 46px;
    height: 28px;

    @include hover(scale, 1.2);
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
  }
}
