@use 'vars' as vars;

@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 41px;
  }

  @include on-desktop-small {
    padding-inline: 54px;
  }

  @include on-desktop-large {
    padding-inline: 104px;
  }
}

@mixin on-tablet {
  @media (min-width: vars.$width-tablet) {
    @content;
  }
}

@mixin on-desktop-small {
  @media (min-width: vars.$width-screen-desktop-small) {
    @content;
  }
}

@mixin on-desktop-large {
  @media (min-width: vars.$width-screen-desktop-large) {
    @content;
  }
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  column-gap: 24;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;
  }

  @include on-desktop-small {
    --columns: 12;
  }
}
