@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;

.footer {
  background-color: #fff;
  margin-top: 120px;

  @include on-tablet {
    margin: 0;
    padding-top: 120px;
  }

  &__content {
    background-color: vars.$c-main-black;
    color: #fff;
    border-radius: 30px 30px 0 0;
    padding-block: 72px 80px;

    @include mix.content-padding-inline;
    @include mix.page-grid;

    row-gap: 80px;

    @include on-tablet {
      row-gap: 120px;
    }

    @include on-desktop-small {
      row-gap: 104px;
    }
  }

  &__title {
    font-size: 32px;
    line-height: 41.6px;

    &--small {
      font-size: 13px;
      font-weight: 700;
      line-height: 19.5px;
      letter-spacing: 3px;
      text-transform: uppercase;
      color: $c-text-white-grey;
      margin-bottom: 8px;
    }
  }

  &__message {
    grid-column: 1 / 3;

    @include on-tablet {
      grid-column: 1 / 5;
    }

    @include mix.on-desktop-small {
      grid-column: 1 / 6;
    }
  }

  &__contacts {
    grid-column: 1 / 3;
    display: flex;
    flex-direction: column;
    gap: 48px;

    @include on-tablet {
      grid-column: 1 / 5;
      gap: 56px;
    }

    @include mix.on-desktop-small {
      grid-column: 7 / 12;
    }

    @include mix.on-desktop-large {
      grid-column: 8 / 12;
    }

    &__call {
      &--tel {
        font-family: 'Open Sans', sans-serif;
        font-size: 24px;
        font-weight: 600;
        line-height: 36px;
        color: #fff;
        text-decoration: none;

        &:hover {
          @include mix.hover(color, vars.$c-main-blue);
        }
      }
    }

    &__visit {
      &--map {
        font-family: 'Open Sans', sans-serif;
        font-size: 24px;
        font-weight: 600;
        line-height: 36px;
        color: #fff;
        text-decoration: none;

        &:hover {
          @include mix.hover(color, vars.$c-main-blue);
        }
      }
    }

    &__social {
      &--all {
        display: flex;
        gap: 32px;
      }

      &--facebook {
        width: 32px;
        height: 32px;
        display: block;
        text-decoration: none;
        background-image: url(../icons/facebook.svg);
        background-repeat: no-repeat;
        background-position: center;
        transition: all vars.$transition-time;

        &:hover {
          background-image: url(../icons/facebook-blue.svg);
        }
      }

      &--twitter {
        width: 32px;
        height: 32px;
        display: block;
        text-decoration: none;
        background-image: url(../icons/twitter.svg);
        background-repeat: no-repeat;
        background-position: center;
        transition: all vars.$transition-time;

        &:hover {
          background-image: url(../icons/twitter-blue.svg);
        }
      }

      &--instagram {
        width: 32px;
        height: 32px;
        display: block;
        text-decoration: none;
        background-image: url(../icons/instagram.svg);
        background-repeat: no-repeat;
        background-position: center;
        transition: all vars.$transition-time;

        &:hover {
          background-image: url(../icons/instagram-blue.svg);
        }
      }
    }
  }

  &__nav {
    grid-column: 1 / 3;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 67px;

    @include on-tablet {
      grid-column: 1 / 7;
      gap: 24px;
    }

    @include mix.on-desktop-small {
      grid-column: 1 / 13;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }
}
