@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;

.header {
  margin: 0;
  padding: 0;
  width: 100%;
  background-color: vars.$c-main-black;
  color: #fff;
  border-radius: 0 0 30px 30px;
  margin-bottom: 120px;

  &__content {
    margin: 0 auto;

    @include mix.page-grid;

    @include on-tablet {
      margin-block: 56px 148px;
    }
  }

  &__main {
    padding-block: 48px;
    grid-column: 1 / 3;

    @include content-padding-inline;

    @include on-tablet {
      grid-column: 1 / 7;
    }

    @include on-desktop-small {
      grid-column: 1 / 6;
    }
  }

  &__title {
    margin: 0 0 32px;
    padding: 0;
    font-size: 52px;
    line-height: 62.4px;
    letter-spacing: -2px;
    grid-column: 1 / 3;

    @include on-tablet {
      grid-column: 1 / 7;
      font-size: 64px;
      line-height: 68px;
    }

    @include on-desktop-small {
      grid-column: 1 / 6;
    }
  }

  &__text {
    margin: 0 0 72px;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    grid-column: 1 / 3;

    @include on-tablet {
      grid-column: 1 / 5;
    }

    @include on-desktop-small {
      grid-column: 1 / 6;
    }
  }

  &__button {
    margin-bottom: 48px;

    @include on-tablet {
      grid-column: 1 / 4;
      margin-bottom: 147px;
    }
  }
}
