@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;

.form {
  margin-top: 56px;

  &__input {
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    height: 48px;
    background-color: vars.$c-main-black;
    color: #fff;
    border: none;
    border-bottom: 1px solid #fff;
    font-size: 15px;
    line-height: 22.5px;
    padding-left: 16px;
    margin-bottom: 48px;
    outline: none;

    &:focus {
      border-color: vars.$c-main-blue;
    }

    &--textarea {
      resize: none;
    }
  }
}
