@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;

.results {
  background-color: #fff;
  width: calc(100% + 40px);

  @include on-tablet {
    width: calc(100% + 82px);
    background-image: url(../images/Shapes.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto 100%;
  }

  @include on-desktop-small {
    width: calc(100% + 108px);
  }

  @include on-desktop-large {
    width: calc(100% + 208px);
  }

  &__content {
    @include mix.content-padding-inline;
  }

  &__title {
    margin: 0 auto;
  }

  &__text {
    max-width: 750px;
    font-family: 'Open Sans', sans-serif;
    font-size: 22px;
    font-weight: 600;
    line-height: 33px;
    color: vars.$c-text-grey;
    margin: 0 auto 64px;
  }

  &__button {
    margin: 0 auto 64px;
  }
}
