@use '../utils/vars' as vars;
@use '../utils/mixins' as mix;
@use '../utils/extends' as ext;

.icon {
  display: block;
  width: 24px;
  height: 24px;
  text-align: center;
  line-height: 24px;

  &--close {
    background-image: url(../icons/icon-close-white.svg);
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: center;

    @include hover(scale, 1.2);
  }

  &--services {
    background-image: url(../icons/services-arrow.svg);
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: center;
  }
}

.arrow--left {
  display: block;
  width: 32px;
  height: 32px;
  background-image: url('../images/slider/arrow-left.svg');
  background-repeat: no-repeat;
  background-size: 6px 9px;
  background-position: center;
  background-color: #fff;
  border-radius: 50%;

  @include hover(scale, 1.2);
}

.arrow--right {
  display: block;
  width: 32px;
  height: 32px;
  background-image: url('../images/slider/arrow-right.svg');
  background-repeat: no-repeat;
  background-size: 6px 9px;
  background-position: center;
  background-color: #fff;
  border-radius: 50%;

  @include hover(scale, 1.2);
}
